.content h1 a {
	position: relative;
	top: 5px;
	margin-right: 21px;
	cursor: pointer;
}
.saved-item-exit {
	opacity: 1;
}
.saved-item-exit-active {
	opacity: 0;
	transition: opacity 400ms ease-in;
}
@media (max-width: 768px) {
	.content h1 a img {
		width: 25px;
	}
	.content h1 a {
		top: 3px;
		margin-right: 12px;
	}
}
