.noSaved {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	justify-content: center;
	padding-top: 70px;
	padding-bottom: 70px;
}
.noSaved .button {
	width: 270px;
}
.noSaved_smile {
	margin-bottom: 24px;
}
h3 {
	font-weight: 600;
	font-size: 24px;
}
.noSaved h3 {
	margin-bottom: 11px;
}
.noSaved p {
	margin-bottom: 70px;
	opacity: 0.4;
	font-size: 16px;
}
@media (max-width: 768px) {
	.noSaved p {
		margin-bottom: 40px;
	}
}
@media (max-width: 470px) {
	.noSaved {
		padding-top: 0;
		padding-bottom: 0;
		min-height: calc(100vh - 70px - 17px - 30px);
	}
	.noSaved_smile img {
		width: 40px;
	}
	.noSaved_smile {
		margin-bottom: 0;
	}
	.noSaved p {
		font-size: 10px;
		margin-bottom: 20px;
	}
	h3 {
		font-size: 18px !important;
	}
	.button {
		width: 200px !important;
		font-size: 12px !important;
		height: 40px !important;
	}
	.button span {
		top: 12px !important;
		left: 20px !important;
	}
	.button span img {
		width: 12px !important;
	}
}
@media (max-width: 425px) {
	.noSaved {
		min-height: calc(100vh - 50px - 17px - 30px);
	}
}
